@import url(//fonts.googleapis.com/earlyaccess/notosanstc.css);
body {
  background-color: #fff;
  padding: 50px;
  font: 14px/1.5 "Noto Sans TC", sans-serif;
  color: #595959;
  font-weight: 400; }

::-webkit-scrollbar {
  width: 1px;
  height: 1px; }

::-webkit-scrollbar-thumb {
  background-color: #e6e6e6; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 10px; }

p,
ul {
  margin: 0 0 20px; }

a {
  text-decoration: none;
  color: #39c; }

ul {
  padding-left: 20px; }

hr {
  border: 0;
  background: #e5e5e5;
  height: 1px;
  margin: 0 0 20px; }

@media screen and (max-width: 720px) {
  body {
    word-wrap: break-word; } }

@media screen and (max-width: 480px) {
  body {
    padding: 15px; } }

/* timeline  styles */
.timeline ul {
  height: 350px;
  overflow-y: scroll;
  overflow-x: hidden;
  -webkit-padding-start: 0;
          padding-inline-start: 0; }
  .timeline ul li {
    list-style-type: none;
    position: relative;
    width: 6px;
    margin: 0 auto;
    padding-top: 35px;
    background: #d8d8d8; }
    .timeline ul li::after {
      content: '';
      position: absolute;
      left: -125%;
      bottom: 40%;
      -webkit-transition: all 0.5s ease-in-out;
      transition: all 0.5s ease-in-out;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      background: inherit; }
    .timeline ul li:hover::after {
      background: #999797;
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      cursor: pointer; }
    .timeline ul li div {
      position: relative;
      width: 180px;
      padding: 15px 15px 0 15px;
      visibility: hidden;
      opacity: 0;
      -webkit-transition: all .5s ease-in-out;
      transition: all .5s ease-in-out; }
      .timeline ul li div::before {
        content: '';
        position: absolute;
        bottom: 15px;
        width: 0;
        height: 0;
        border-style: solid; }
      .timeline ul li div time {
        float: left;
        width: 100%; }
    .timeline ul li:nth-child(odd) div {
      left: 40px;
      -webkit-transform: translate3d(200px, 0, 0);
              transform: translate3d(200px, 0, 0); }
      .timeline ul li:nth-child(odd) div::before {
        left: -15px;
        border-width: 8px 16px 8px 0;
        border-color: transparent #b3484873 transparent transparent; }
    .timeline ul li:nth-child(even) div {
      left: -244px;
      text-align: right;
      -webkit-transform: translate3d(-200px, 0, 0);
              transform: translate3d(-200px, 0, 0); }
      .timeline ul li:nth-child(even) div::before {
        right: -15px;
        border-width: 8px 0 8px 16px;
        border-color: transparent transparent transparent #b3484873; }
    .timeline ul li.in-view div {
      -webkit-transform: none;
              transform: none;
      visibility: visible;
      opacity: 1;
      bottom: 30px; }

@media screen and (max-width: 600px) {
  .timeline ul li {
    margin-left: 20%; }
    .timeline ul li:nth-child(even) div {
      left: 40px;
      text-align: left;
      text-align: initial; }
      .timeline ul li:nth-child(even) div::before {
        left: -15px;
        border-width: 8px 16px 8px 0;
        border-color: transparent #b3484873 transparent transparent; } }

.wrapper {
  width: 860px;
  margin: 0 auto; }

.header {
  width: 270px;
  float: left;
  position: fixed;
  -webkit-font-smoothing: subpixel-antialiased; }

.avatar {
  min-width: 100%; }

.headerIcons a {
  color: #000;
  color: initial; }

.section {
  width: 500px;
  float: right;
  padding-bottom: 50px; }
  .section .portfolio_block img[class=qr-official] {
    width: 118px;
    height: 118px; }

.footer {
  width: 270px;
  float: left;
  position: fixed;
  bottom: 10px;
  -webkit-font-smoothing: subpixel-antialiased; }

@media screen and (max-width: 960px) {
  .header,
  .section,
  .footer {
    float: none;
    position: static;
    width: auto; }
  .wrapper {
    width: auto;
    margin: 0; }
  .header {
    padding-right: 320px; }
  .section {
    padding: 20px 0;
    margin: 0 0 20px; } }

@media screen and (max-width: 720px) {
  .header {
    padding: 0; } }

